body {
  font-family: sans-serif;
}

.breed-select {
  width: 250px;
  font-size: 16px;
  display: none;
}

.breed-select.js-visible {
  display: block;
}

.cat-info {
  width: calc(100% - 200px);
  align-items: center;
  gap: 35px;
  margin: 30px auto 0;
  display: none;
}

.cat-info.js-visible {
  display: flex;
}

.cat-image {
  width: 300px;
  height: 300px;
  object-fit: cover;
}

.evidance {
  font-weight: 600;
}

.loader {
  color: green;
  display: none;
}

.loader.js-visible {
  display: block;
}

.error {
  color: #cd0606;
  display: none;
}

.error.js-visible {
  display: block;
}

.loader-info {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #ff3d00;
  border-color: #ff3d00 rgba(0, 0, 0, 0);
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

